.App {
  text-align: center;
}

.question-card {
  /* Center the div  */
  margin: 0 auto;

  /* Dimensions  */
  width: 80%; /* Can be in percentage also. */
  height: auto;

  /* Visual  */
  background-color: rgb(23, 81,18);
  padding: 16px;
  border-radius: 16px;
  color: white;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}

ul {
  list-style: none;
}

li {
  margin-top: 8px;
  background-color: rgba(255, 255, 255, 0.86);
  padding: 16px;
  border: 3px solid rgb(0, 0, 0);
  border-radius: 20px;
  font-size: 20px;
  color: black;
}

.question-text {
  color: rgb(255, 255, 255);
  font-size: 24px;
}

/* Final Results  */

.final-results {
  /* Center the div  */
  margin: 0 auto;

  /* Dimensions  */
  width: 50%; /* Can be in percentage also. */
  height: auto;
  margin-top: 64px;

  /* Visual  */
  background-color: gray;
  padding: 16px;
  border-radius: 16px;
  color: white;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}

button {
  background-color: rgb(23, 81,18);
  border: none;
  color: white;
  padding: 16px 24px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  border-radius: 24px;
  margin-top: 8px;
}
.disabled {
  pointer-events: none; /* Disable clicks */
  opacity: 0.6; /* Make it look disabled */
  cursor: default; /* Change cursor */
}
.feedback {
  /* Center the div  */
  margin: 0 auto;
  /* Dimensions  */
  width: 70%; /* Can be in percentage also. */
  height: auto;

  /* Visual  */
  background-color: rgb(212, 220, 154);
  padding: 16px;
  border-radius: 16px;
  color: rgb(0, 0, 0);
  margin-top: 10px;
}
